      cover: Resize the background image to cover the entire container, 
             even if it has to stretch the image or cut a little bit off one of the edge 
             (image is still in proportion, how much of it is visible depending on the size of the screen)
      contain: Resize the background image to make sure the image is fully visible 
              (might repeat the image if the screen is very big, if background-repeat is set to no-repeat, 
              the extra part will be blank)
      length: Sets the width and height of the background image. The first value sets the width, 
              the second value sets the height. If only one value is given, the second is set to "auto".
      percentage: Sets the width and height of the background image in percent of the parent element. 
              The first value sets the width, the second value sets the height. 
              If only one value is given, the second is set to "auto"
     */
}


/* CSS for MORE button*/
.exploreBtn {
  align-items: center;
  background-color: #fee6e3;
  border: 2px solid #111;
  border-radius: 8px;
  box-sizing: border-box;
  color: #111;
  cursor: pointer;
  display: flex;
  font-family: Inter,sans-serif;
  font-size: 16px;
  height: 48px;
  justify-content: center;
  line-height: 24px;
  max-width: 100%;
  padding: 0 25px;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.exploreBtn:after {
  background-color: #111;
  border-radius: 8px;
  content: "";
  display: block;
  height: 48px;
  left: 0;
  width: 100%;
  position: absolute;
  top: -2px;
  -webkit-transform: translate(8px, 8px);
          transform: translate(8px, 8px);
  transition: -webkit-transform .2s ease-out;
  transition: transform .2s ease-out;
  transition: transform .2s ease-out, -webkit-transform .2s ease-out;
  z-index: -1;
}

.exploreBtn:hover:after {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}

.exploreBtn:active {
  background-color: #ffdeda;
  outline: 0;
}

.exploreBtn:hover {
  outline: 0;
}

@media (min-width: 768px) {
  .exploreBtn {
    padding: 0 40px;
  }
} 
