    min-height: 86vh !important;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background-image: url(/wenxin/static/media/intro_background.7ef83cdc.jpg) !important;
    background-repeat: no-repeat !important;
    background-size:cover !important; 
}

.kpop-title{
    font-family:cursive;
    font-size: xx-large;
    margin-left: 4%;
    margin-top: 0;
}

.kpop-content{
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    height:100%;
    margin-left:3%;
    margin-top:1%;
}

#frame-container {
    list-style: none;
	list-style-type: none;
    width: 40%;
    margin-top: 0;
    text-align: center;
}

.frame {
	display: inline-block;
	padding: 10px;
	border-width: 10px;
	border-style: solid;
	border-color: #2F2D2D #434040 #4F4C4C #434040;
	background: #f5f5f5;
	background-image: linear-gradient(#E5E4DF, #CDCDC6);
	box-shadow: inset 0 2px 5px rgba(0,0,0,.6),0 5px 2px rgba(0,0,0,.1), 0 10px 20px rgba(0,0,0,.8);
	position: relative;
	overflow: hidden;
    list-style: none;
	list-style-type: none;
	margin: 0px;
	padding: 0px;
	text-align: center;
}

.frame::before {
	content: "";
	position: absolute;
	top: -175px;
	right: -20%;
	width: 400px;
	height: 400px;
	transform: rotateZ(-40deg);
	-webkit-transform: rotateZ(-40deg);
	-moz-transform: rotateZ(-40deg);
	-o-transform: rotateZ(-40deg);
	background-image: linear-gradient(rgba(255,255,255,.4), rgba(255,255,255,0));
}
.frame img {
	/* border-width: 2px;
	border-style: solid;
	border-color: #BBBAB4 #C7C7BF #E5E4DF #C7C7BF; */
	box-shadow:  0 -1px 1px rgba(0,0,0,.1), 0 1px 1px 1px rgba(255,255,255,.7);
    width:100%;
    margin:0;
}

.title{
    font-family:cursive;
